.container-all {
  background-color: #212121;
  width: 100%;
  height: 100%;
  padding: 0px;
  margin: 0;
  margin-left: 0;
  overflow-x: hidden;
  overflow-y: hidden;
}
::-webkit-scrollbar {
  display: none;
}
.container-all {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
